/* ==============================================================
   Widget Views
   style declarations for widget_views.xhtml
   ==============================================================*/
.widget-views {
	.widget__body {
	   li.active {
	     a {
	       font-weight: 600;
	       color: @lk-content-box;
		   
		   &:hover {
				color: @lh-content-box;
		   }
	     }
	   }
	 }
}

