/* ==============================================================
   Widget RSS
   style declarations for rssFeed.xhtml
   ==============================================================*/
.widget-rss {
	
	&__entry-info {
		line-height: 1.1;
		border-bottom: @border;
		
		&:last-child {
			border-bottom: none;
		}
	}
	
	&__entry-link {
		font-weight: 400;
	}
}

/* BREAKPOINT FOR LARGE DESKTOPS */
